}

.btn-link--white, .btn-link--white:focus {
  color: rgba(255, 255, 255, 0.8);
}

.btn-link--white:hover {
  color: #fff;
}

.btn-collapse {
  padding-left: 0;
  padding-right: 0;
}

.btn-wide {
  padding-left: 15px;
  padding-right: 15px;
}

.btn-wide-lg {
  padding-left: 30px;
  padding-right: 30px;
}

html,
body,
.app-container {
  background-color: #f2f2f2;
}

.app-container {
  display: block;
  width: 100%;
  text-align: center;
  font-size: 14px;
}

.app-body {
  display: inline-block;
  text-align: left;
  width: 100%;
  max-width: 580px;
}

.app-body-content {
  background: #fff;
}

.app-logo-block {
  padding: 30px 0;
}

.app-title {
  margin: 0;
  padding-bottom: 30px;
}

.app-logo {
  display: inline-block;
  width: 106px;
  height: 45px;
}

.app-footer {
  background-color: #f2f2f2;
  font-size: 12px;
  padding: 30px 15px;
}
